.fullscreen-wrapper{position:relative;width:100%;height:100%;display:flex;align-items:center;justify-content:center;background:rgba(var(--color-grey-dark-2-rgb), 0.85);-webkit-backdrop-filter:blur(4px);backdrop-filter:blur(4px);border-radius:16px;overflow:hidden;transition:all .4s cubic-bezier(0.4, 0, 0.2, 1);box-shadow:0 10px 30px -10px rgba(0,0,0,.2),0 0 0 1px hsla(0,0%,100%,.05)}.fullscreen-wrapper .fullscreen-content{width:100%;height:100%;display:flex;align-items:center;justify-content:center;padding:20px;position:relative;z-index:1}.fullscreen-wrapper .fullscreen-content.game-container{padding:10px}.fullscreen-wrapper .fullscreen-content.game-container canvas{max-width:100%;max-height:100%;object-fit:contain}@media(max-width: 768px){.fullscreen-wrapper .fullscreen-content.game-container{padding:5px}}.fullscreen-wrapper .fullscreen-toggle{position:absolute;top:16px;right:16px;width:40px;height:40px;border-radius:50%;background:var(--color-grey-dark-3);border:1px solid hsla(0,0%,100%,.1);color:var(--color-text);cursor:pointer;display:flex;align-items:center;justify-content:center;padding:0;z-index:2;transition:all .2s ease}.fullscreen-wrapper .fullscreen-toggle:hover{background:var(--color-grey-dark-4);transform:scale(1.05)}.fullscreen-wrapper .fullscreen-toggle:active{transform:scale(0.95)}.fullscreen-wrapper .fullscreen-toggle svg{width:24px;height:24px}@media(max-width: 768px){.fullscreen-wrapper .fullscreen-toggle{top:8px;right:8px;width:32px;height:32px}.fullscreen-wrapper .fullscreen-toggle svg{width:20px;height:20px}}.fullscreen-wrapper.fullscreen{position:fixed;top:0;left:0;width:100vw;height:100vh;z-index:999;border-radius:0;background:var(--color-grey-dark-1);padding:0}.fullscreen-wrapper.fullscreen .fullscreen-content{padding:clamp(10px,3vw,20px);padding-top:calc(var(--header-height, 60px) + clamp(10px,3vw,20px))}.fullscreen-wrapper.fullscreen .fullscreen-content.game-container{padding:clamp(5px,2vw,10px);padding-top:calc(var(--header-height, 60px) + clamp(5px,2vw,10px))}@media screen and (max-width: 768px){.fullscreen-wrapper.fullscreen .fullscreen-content{padding:clamp(10px,2vw,15px);padding-top:calc(var(--header-height, 60px) + clamp(10px,2vw,15px))}.fullscreen-wrapper.fullscreen .fullscreen-content.game-container{padding:5px;padding-top:calc(var(--header-height, 60px) + 5px)}}@media screen and (max-height: 600px){.fullscreen-wrapper.fullscreen .fullscreen-content{padding:10px;padding-top:calc(var(--header-height, 40px) + 10px)}.fullscreen-wrapper.fullscreen .fullscreen-content.game-container{padding:5px;padding-top:calc(var(--header-height, 40px) + 5px)}}.fullscreen-wrapper.fullscreen .fullscreen-toggle{top:calc(var(--header-height, 60px) + 16px)}@media(max-width: 768px){.fullscreen-wrapper.fullscreen .fullscreen-toggle{top:calc(var(--header-height, 60px) + 8px)}}